<template>
<div class="container">
  <!--  -->
  <mt-header title="" fixed>
    <mt-button icon="back" slot="left" @click="back"></mt-button>
  </mt-header>
  <!--  -->
  <!--  -->
  <div class="express">
    <span>{{logisticInfo.shipperCodeMean + '()'}}</span>
    <span>{{logisticInfo.logisticCode}}</span>
    <div class="mask" v-if="maskShow">
      <p></p>
    </div>
  </div>
  <!--  -->
  <!--  -->
  <div class="logistic-info">
    <div class="logistic-container" v-for="(val) in logisticInfo.logisticInfo" :key="val.acceptTime">
      <div class="date-time">
        <p>{{val.date}}</p>
        <p class="time">{{val.time}}</p>
      </div>
      <div class="info">
        <span>{{val.acceptStation}}</span>
      </div>
    </div>
  </div>
  <!--  -->
</div>
</template>

<script>
export default {
  data () {
    return {
      logisticInfo: {}, // 
      maskShow: false // 
    }
  },
  created: function () {
    this.initLogistic()
  },
  methods: {
    // 
    back: function () {
      this.$router.back()
    },
    // 
    initLogistic: function () {
      let data = {
        dinDanId: this.$route.params.orderId
      }
      this.axios.post('/water/logistic/list', data)
        .then(res => {
          if (res.data.length === 0) {
            this.maskShow = true
            return 0
          }
          if (res.data[0].shipperCode === 'SF') {
            res.data[0].shipperCodeMean = ''
          } else if (res.data[0].shipperCode === 'YTO') {
            res.data[0].shipperCodeMean = ''
          } else if (res.data[0].shipperCode === 'ZTO') {
            res.data[0].shipperCodeMean = ''
          } else if (res.data[0].shipperCode === 'YD') {
            res.data[0].shipperCodeMean = ''
          } else if (res.data[0].shipperCode === 'ANE') {
            res.data[0].shipperCodeMean = ''
          }
          res.data[0].logisticInfo.forEach((val, index) => {
            res.data[0].logisticInfo[index].date = val.acceptTime.substring(5, 10)
            res.data[0].logisticInfo[index].time = val.acceptTime.substring(11, 16)
          })
          this.logisticInfo = res.data[0]
        })
        .catch(() => {
          this.$toast('')
        })
    }
  }
}
</script>

<style scoped>
.container {
  padding-top: 40px;
  background: #F9F9F9;
  padding-bottom: 1rem;
  overflow: auto;
}
/*  */
.express {
  height: 4rem;
  line-height: 3rem;
  width: 96%;
  margin: 0 auto;
  background: #FFFFFF;
  margin-top: 0.5rem;
  border-radius: 10px;
  padding: 0.5rem;
  position: relative;
}
.mask {
  line-height: 4rem;
  width: 96%;
  margin: 0 auto;
  background: #FFFFFF;
  border-radius: 10px;
  position: absolute;
  top: 0;
}
/*  */
.logistic-info {
  width: 96%;
  margin: 0 auto;
  background: #FFFFFF;
  min-height: 16rem;
  margin-top: 0.5rem;
  padding: 1rem;
  border-radius: 10px;
  text-align: left;
  font-size: 14px;
  padding-bottom: 2rem;
}
.date-time {
  width: 20%;
  float: left;
  text-align: right;
  padding-right: 1rem;
}
.info {
  width: 80%;
  float: left;
}
.date-time .time {
  margin-top: 0.3rem;
  font-size: 12px;
  color: #9C9C9C;
}
.logistic-container {
  margin-top: 1rem;
  overflow: hidden;
}
</style>



