<template>
<div class="container">
  <!--  -->
  <mt-header title="" fixed>
    <mt-button slot="left" ></mt-button>
  </mt-header>
  <!--  -->
  <!--  -->
  <div class="icon-container">
    <img src="../../assets/icon/order/completeOrder.svg" alt="">
    <img src="../../assets/icon/order/completeOrder2.svg" alt="" class="tips">
  </div>
  <!--  -->
  <!--  -->
  <div class="but-container">
    <mt-button type="primary" class="home-btn" @click="backHome"></mt-button>
    <mt-button type="primary" plain @click="queryOrder"></mt-button>
  </div>
  <!--  -->
</div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  mounted () {
    // 
    if (window.history && window.history.pushState) {
      window.addEventListener('popstate', () => {
        this.$router.go(0)
        this.$router.push({name: 'Home', params: {type: 'home'}})
      }, true)
    }
  },
  methods: {
    // 
    backHome: function () {
      this.$router.go(0)
      this.$router.push({name: 'Home', params: {type: 'home'}})
    },
    // 
    queryOrder: function () {
      this.$router.push({name: 'OrderList', params: {orderType: '1'}})
    }
  }
}
</script>

<style scoped>
  /*  */
.icon-container {
  padding-top: 80px;
  width: 100%;
}
.tips {
  margin-top: 3rem;
  height: 2.5rem;
}
.but-container {
  height: 4rem;
  margin-top: 4rem;
}
.home-btn {
  margin-right: 1.5rem;
}
</style>



