<template>
<div class="container">
  <!--  -->
  <mt-header title="" fixed>
    <mt-button icon="back" slot="left" @click="back"></mt-button>
  </mt-header>
  <!--  -->
  <!--  -->
  <div class="mid">
    <div class="district" @click="selectedP">
      <input type="text" placeholder="" readonly="true" v-model="province"/>
      <input type="text" placeholder="" readonly="true" v-model="city"/>
      <input type="text" placeholder="" readonly="true" v-model="district"/>
    </div>
    <mt-field label="" placeholder="" v-model="userName"></mt-field>
    <mt-field label="" placeholder="" v-model="phoneNum"></mt-field>
    <mt-field label="" placeholder="" type="textarea" rows="2" v-model="addressInfo" class="address-info"></mt-field>
  </div>
  <!--  -->
  <!--  -->
  <div class="footer-container">
     <mt-button type="primary" size="large" @click="saveClick()"></mt-button>
  </div>
  <!--  -->
  <!--  -->
  <mt-popup v-model="popupVisible" position="bottom" class="popup-container">
    <div class="address">
      <div>
        <mt-button size="small" type="primary" class="confirm" @click="confirmAddress()"></mt-button>
        <mt-button size="small" class="cal" @click="cancle()"></mt-button>
      </div>
      <mt-picker :slots="areaList" @change="selectArea" :visibleItemCount="5" :itemHeight="27"></mt-picker>
    </div>
  </mt-popup>
  <!--  -->
</div>
</template>

<script>
export default {
  data () {
    return {
      userName: '', // 
      phoneNum: '', // 
      province: '', // 
      city: '', // 
      district: '', // 
      addressInfo: '', // 
      selectedProvince: '', // 
      selectedCity: '', // 
      selectedDistrict: '', // 
      popupVisible: false, // 
      provinceArry: [], // 
      cityArry: [], // 
      areaList: [
        {
          flex: 1,
          values: [''],
          className: 'slot2',
          textAlign: 'center'
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        }, {
          flex: 1,
          values: [''],
          className: 'slot2',
          textAlign: 'center'
        }, {
          divider: true,
          content: '-',
          className: 'slot2'
        }, {
          flex: 1,
          values: [''],
          className: 'slot2',
          textAlign: 'center'
        }
      ]
    }
  },
  created: function () {
    this.initAddress()
    this.axios.post('water/ChinaAddress/selectP')
      .then(res => {
        this.provinceArry = res.data
        let provArr = []
        res.data.forEach((val, index) => {
          provArr.push(val.name)
        })
        this.areaList[0].values = provArr
      })
  },
  methods: {
    // 
    back: function () {
      this.$router.back()
    },
    // 
    initAddress: function () {
      let data = {addressId: this.$route.params.addressId}
      this.axios.post('water/address/info', data)
        .then((res) => {
          this.province = res.data[0].addressProvince
          this.city = res.data[0].addressCity
          this.district = res.data[0].addressDistrict
          this.userName = res.data[0].name
          this.phoneNum = res.data[0].phoneNum
          this.addressInfo = res.data[0].address
        })
    },
    selectedP: function () {
      this.popupVisible = true
    },
    // promise
    getArea: function (data) {
      let promise = new Promise((resolve, reject) => {
        this.axios.post('water/ChinaAddress/select', data)
          .then(function (res) {
            resolve(res)
          })
      })
      return promise
    },
    // 
    selectArea: function (picker, values) {
      let selectedProv = picker.getSlotValue(0)
      let prov = this.provinceArry.find((val) => {
        if (val.name === selectedProv) {
          return val
        }
      })
      if (prov === undefined) {
        return
      }
      // 
      let data = {parent: prov.value}
      this.getArea(data)
        .then(res => {
          this.cityArry = res.data
          let cityArr = []
          res.data.forEach((val, index) => {
            cityArr.push(val.name)
          })
          this.areaList[2].values = cityArr
          let selectedCity = picker.getSlotValue(1)
          let city = this.cityArry.find((val) => {
            if (val.name === selectedCity) {
              return val
            }
          })
          // 
          if (city !== undefined) {
            let data = {parent: city.value}
            return this.getArea(data)
          }
        })
        .then(res => {
          if (res !== undefined) {
            this.distictArry = res.data
            let distictArr = []
            res.data.forEach((val, index) => {
              distictArr.push(val.name)
            })
            this.areaList[4].values = distictArr
          }
        })
      this.selectedProvince = picker.getSlotValue(0)
      this.selectedCity = picker.getSlotValue(1)
      this.selectedDistrict = picker.getSlotValue(2)
    },
    // 
    confirmAddress: function () {
      this.province = this.selectedProvince
      this.city = this.selectedCity
      this.district = this.selectedDistrict
      this.popupVisible = false
    },
    // 
    cancle: function () {
      this.popupVisible = false
    },
    // 
    saveClick: function () {
      var data = {
        phoneNum: this.phoneNum,
        name: this.userName,
        address: this.addressInfo,
        addressDistrict: this.district,
        addressCity: this.city,
        addressProvince: this.province,
        custId: this.$store.getters.getOpenid,
        addressId: this.$route.params.addressId
      }
      // 
      if (this.$trim(this.province) === null || this.$trim(this.province) === '') {
        return this.$toast('')
      }
      if (this.$trim(this.city) === null || this.$trim(this.city) === '') {
        return this.$toast('')
      }
      if (this.$trim(this.district) === null || this.$trim(this.district) === '') {
        return this.$toast('')
      }
      if (this.$trim(this.userName) === null || this.$trim(this.userName) === '') {
        return this.$toast('')
      }
      if (this.$trim(this.phoneNum) === null || this.$trim(this.phoneNum) === '') {
        return this.$toast('')
      }
      if (this.$trim(this.addressInfo) === null || this.$trim(this.addressInfo) === '') {
        return this.$toast('')
      }
      // 
      if (this.userName.length > 20) {
        return this.$toast('')
      }
      if (this.phoneNum.length > 16) {
        return this.$toast('')
      }
      if (this.addressInfo.length > 30) {
        return this.$toast('')
      }
      this.axios.post('water/address/update2', data)
        .then(() => {
          this.$toast('')
          this.$router.back()
        })
        .catch(() => {
          this.$toast('')
        })
    }
  }
}
</script>

<style scoped>
/*  */
.mid{
  padding-top: 40px;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.district {
  padding-top: 0.5rem;
  margin-bottom: 1rem;
  margin-top: 1rem;
}
input[type=text] {
  border: 0;
  display: inline-block;
  width: 32%;
  text-align: center;
  font-size: 14px;
  height: 3rem;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
}
/*  */
.footer-container {
  width: 100%;
  height: 4.5rem;
  position: fixed;
  bottom: 0;
  background-color: #FFFFFF;
  padding: 1rem 2rem;
}
/* */
.popup-container {
  width: 100%;
}
.address {
  height: 14rem;
  width: 100%;
  padding-top: 3rem;
}
.address-info {
  margin-top: 0.5rem;
}
.confirm {
  position: absolute;
  right: 1rem;
  top: 0.7rem;
}
.cal {
  position: absolute;
  left: 1rem;
  top: 0.7rem;
}
</style>



