<template>
<div class="container">
  <!--  -->
  <mt-header title="" fixed>
    <mt-button icon="back" slot="left" @click="back"></mt-button>
    <mt-button slot="right" ></mt-button>
  </mt-header>
  <!--  -->
  <!--  -->
  <div class="pro-img">
    <img :src="'/static' + proData.integralProInfo" alt="">
  </div>
  <!--  -->
  <!--  -->
  <div class="introduct-pro">
    <p class="title">
      <span class="flag"></span>
      <span class="name">{{ proData.integralProName}}</span>
    </p>
    <p class="pro-integral-container">
      <span class="integral-nums">{{proData.integralProPrice + ''}}</span>
      <span class="tips1"></span>
    </p>
    <div class="tips-container">
      <p class="tips2">:</p>
      <p>1.</p>
      <p>2.</p>
      <p>3.</p>
    </div>
  </div>
  <!--  -->
  <div class="footer">
    <mt-button size="large" type="danger" @click="exchange"></mt-button>
  </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      proData: {}
    }
  },
  created: function () {
    this.initPro()
  },
  methods: {
    // 
    back: function () {
      this.$router.push({name: 'IntergralProList'})
    },
    // 
    initPro: function () {
      let data = {
        integralProId: this.$route.params.productId
      }
      this.axios.post('water/integralPro/selectOne', data)
        .then(res => {
          this.proData = res.data[0]
        })
        .catch(() => {
          this.$toast('')
        })
    },
    // 
    exchange: function () {
      let data = {
        custId: this.$store.getters.getOpenid
      }
      this.axios.post('water/vip/list', data)
        .then(res => {
          console.log(res)
          if (parseInt(res.data[0].user[0].vip.integral) < parseInt(this.proData.integralProPrice)) {
            this.$toast('')
          } else {
            this.$router.push({name: 'IntegralOrder', params: {productId: this.proData.integralProId}})
          }
        })
    }
  }
}
</script>

<style scoped>
.container {
  padding-top: 40px;
}
.pro-img {
  height: 15rem;
  line-height: 15rem;
  width: 100%;
  padding: 1rem 1rem;
}
.pro-img img {
  height: 95%;
}
/*  */
.introduct-pro {
  text-align: left;
  width: 100%;
  padding-left: 1rem;
  font-size: 14px;
  padding-right: 1rem;
}
.title {
  line-height: 2rem;
  margin-bottom: 0.6rem;
}
.flag {
  border: 1px solid #FF4949;
  padding: 0.2rem;
  color: #FF4949;
  font-size: 12px;
  border-radius: 3px;
  margin-right: 1rem;
}
.name {
  font-size: 16px;
  font-weight: 550;
}
.pro-integral-container {
  line-height: 2rem;
  margin-bottom: 0.3rem;
}
.integral-nums {
  color: red;
  margin-right: 1.3rem;
  font-size: 15px;
}
.tips1 {
  font-size: 12px;
  color: #9C9C9C;
  margin-bottom: 0.8rem;
}
.tips-container p {
  margin-bottom: 0.3rem;
}
.tips-container {
  color: #9C9C9C;
  font-size: 13px;
}
.tips2 {
  color: #000000;
}
/*  */
.footer {
  margin: 1rem 0.5rem;
}
</style>



