<template>
<div class="container">
  <!--  -->
  <mt-header title="" fixed>
    <mt-button icon="back" slot="left" @click="back"></mt-button>
  </mt-header>
  <!--  -->
  <!--  -->
  <div class="mid-container">
    <mt-field label="" placeholder="" v-model="name"></mt-field>
    <mt-field label="" placeholder="" v-model="phoneNum"></mt-field>
  </div>
  <!--  -->
  <!--  -->
  <div class="footer">
    <mt-button type="primary" size="large" @click="updateData()"></mt-button>
  </div>
  <!--  -->
</div>
</template>

<script>
export default {
  data () {
    return {
      name: '', // 
      phoneNum: '' // 
    }
  },
  created: function () {
    this.getUserData()
  },
  methods: {
    // 
    back: function () {
      this.$router.back()
    },
    // 
    getUserData: function () {
      let data = {
        custId: this.$store.getters.getOpenid
      }
      this.axios.post('water/user/selectNamePhone', data)
        .then(res => {
          this.name = res.data[0].realName
          this.phoneNum = res.data[0].phoneNum
        })
        .catch(() => {
          this.$toast('')
        })
    },
    // 
    updateData: function () {
      let data = {
        custId: this.$store.getters.getOpenid,
        realName: this.name,
        phoneNum: this.phoneNum
      }
      this.axios.post('water/user/updateUser', data)
        .then(res => {
          this.$toast('')
          setTimeout(() => {
            this.$router.back()
          }, 800)
        })
    }
  }
}
</script>

<style scoped>
.container {
  padding-top: 40px;
}
.mid-container {
  height: 100%;
 }
/*  */
.footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 0 2rem 1rem;
}
</style>



