<template>
<div class="container">
  <!--  -->
  <mt-header title="" fixed>
    <mt-button icon="back" slot="left" @click="back"></mt-button>
  </mt-header>
  <!--  -->
  <!--  -->
  <div class="address-container" @click="selectedAddress()">
    <div class="address-icon">
      <img src="../../assets/icon/order/address.svg" alt="" >
    </div>
    <div class="address-info">
      <p>
        <span class="address-info-name">{{'' + address.name}}</span>
        <span class="address-info-phone">{{address.phoneNum}}</span>
      </p>
      <p class="detail-adress">
        <span>{{'' + address.province + address.city + address.district + address.addressInfo}}</span>
      </p>
    </div>
    <div class="none-address" v-if="address.noAddress">
      
    </div>
    <div class="address-selected" >
      <img src="../../assets/icon/order/enter.svg" alt="" >
    </div>
  </div>
  <!--  -->
  <!--  -->
  <div class="goods-container">
    <!--  -->
    <div class="company-name">
      <span class="flag"></span>
      <span></span>
    </div>
    <!--  -->
    <!--  -->
    <div class="goods" v-for="(val) in productList" :key="val.productId">
      <div class="goods-img">
        <img :src="'/static' + val.productInfo" alt="">
      </div>
      <div class="goods-info">
        <p class="goods-name">
          <span>{{val.productName}}</span>
        </p>
        <p class="goods-pice-nums">
          <span class="pice">{{'' + val.productPrice}}</span>
          <span class="nums">{{'x' + val.buyNums}}</span>
        </p>
      </div>
    </div>
    <!--  -->
    <!--  -->
    <div class="freight-container">
      <p>
        <span></span>
        <span class="freight">0</span>
      </p>
    </div>
    <!--  -->
    <!--  -->
    <div class="freight-container">
      <p>
        <span></span>
        <span class="freight">0</span>
      </p>
    </div>
    <!--  -->
    <!--  -->
    <div class="message-container">
      <p>
        <span></span>
        <input type="text" placeholder=":">
      </p>
    </div>
    <!--  -->
    <!--  -->
    <div class="subtotal-container">
      <p>
        <span>{{''+ totalBuyNums +'&nbsp;&nbsp;&nbsp;'}}</span>
        <span>{{''}}</span>
        <span class="subtotal-money">{{''+ subtotalMoney +''}}</span>
      </p>
    </div>
    <!--  -->
  </div>
  <!--  -->
  <!--  -->
  <div class="footer-container">
    <div class="money-container">
      <span>{{''}}</span>
      <span class="money">{{''+ totalMoney +''}}</span>
    </div>
    <div class="genert-order" @click="generateOrder()"></div>
  </div>
  <!--  -->
</div>
</template>

<script>
export default {
  data () {
    return {
      address: { // 
        name: '', // 
        phoneNum: '', // 
        province: '', // 
        city: '', // 
        district: '', // 
        addressInfo: '', // 
        addressId: '', // Id
        noAddress: false // falsetrue
      },
      productList: [], // 
      totalBuyNums: 0, // 
      subtotalMoney: 0, // 
      freight: 0, // 
      discountMoney: 0, // 
      lessMessage: '', // 
      totalMoney: 0, // 
      from: {} // 
    }
  },
  created: function () {
    // 
    this.initAddress()
    // 
    this.initGoods()
  },
  mounted () {
    // 
    if (window.history && window.history.pushState) {
      window.addEventListener('popstate', this.toUrl, true)
    }
  },
  methods: {
    toUrl: function () {
      if (localStorage.getItem('fromPath') === '/ShopCar/shopCar') {
        this.$router.push({name: 'ProductDeatils', params: {productId: localStorage.getItem('productId')}})
        localStorage.removeItem('fromPath')
      } else if (localStorage.getItem('fromPath') === '/ShopCar/shopCarTab') {
        this.$router.push({name: 'Home', params: {type: 'home'}})
        this.$router.go(0)
      } else if (localStorage.getItem('fromPath') === undefined || localStorage.getItem('fromPath') === null || localStorage.getItem('fromPath') === '') {
        this.$router.push({name: 'Home', params: {type: 'product'}})
      } else {
        this.$router.push({path: localStorage.getItem('fromPath')})
        localStorage.removeItem('fromPath')
      }
      window.removeEventListener('popstate', this.toUrl, true)
    },
    // 
    back: function () {
      localStorage.removeItem('selectedAddressId')
      this.$router.push({path: localStorage.getItem('fromPath')})
    },
    // 
    initAddress: function () {
      /**
       * addressid
       * addressid
       * 
       */
      if (localStorage.getItem('selectedAddressId') !== null && localStorage.getItem('selectedAddressId') !== undefined) {
        let data = {
          addressId: localStorage.getItem('selectedAddressId')
        }
        this.axios.post('water/address/info', data)
          .then(res => {
            if (res.data !== null && res.data !== undefined && res.data.length !== 0) {
              this.address.addressId = res.data[0].addressId
              this.address.name = res.data[0].name
              this.address.phoneNum = res.data[0].phoneNum
              this.address.province = res.data[0].addressProvince
              this.address.city = res.data[0].addressCity
              this.address.district = res.data[0].addressDistrict
              this.address.addressInfo = res.data[0].address
            } else { // 
              this.address.noAddress = true
            }
          })
          .catch(() => {
            this.$toast('')
          })
      } else {
        let data = {
          custId: this.$store.getters.getOpenid
        }
        this.axios.post('water/address/default', data)
          .then(res => {
            if (res.data !== null && res.data !== undefined && res.data.length !== 0) {
              this.address.addressId = res.data[0].addressId
              this.address.name = res.data[0].name
              this.address.phoneNum = res.data[0].phoneNum
              this.address.province = res.data[0].addressProvince
              this.address.city = res.data[0].addressCity
              this.address.district = res.data[0].addressDistrict
              this.address.addressInfo = res.data[0].address
            } else { // 
              this.address.noAddress = true
            }
          })
          .catch(() => {
            this.$toast('')
          })
      }
    },
    // 
    selectedAddress: function () {
      this.$router.push({name: 'AddressManager', params: {typed: 'selectedAddress'}})
    },
    // 
    initGoods: function () {
      // 
      if (this.$route.params.orderType === 'buyNow') {
        let data = {
          productId: localStorage.getItem('proId')
        }
        this.axios.post('water/product/info', data)
          .then((res) => {
            this.productList = res.data
            this.productList[0].buyNums = localStorage.getItem('buyNums')
            this.totalBuyNums = localStorage.getItem('buyNums')
            this.subtotalMoney = parseInt(localStorage.getItem('buyNums')) * parseInt(this.productList[0].productPrice)
            this.totalMoney = this.subtotalMoney - parseInt(this.freight) - parseInt(this.discountMoney)
          })
      } else if (this.$route.params.orderType === 'shopCar') {
        this.axios.post('water/shopCar/part', {shopCarId: localStorage.getItem('shopcarIdStr')})
          .then(res => {
            this.totalBuyNums = 0
            this.subtotalMoney = 0
            res.data.forEach((val, index) => {
              this.productList.push({
                productId: val.productId,
                productInfo: val.product.productInfo,
                productName: val.product.productName,
                productPrice: val.product.productPrice,
                buyNums: val.buyNum
              })
              this.totalBuyNums += parseInt(val.buyNum)
              this.subtotalMoney += parseInt(val.buyNum) * parseInt(val.product.productPrice)
            })
            this.totalMoney = this.subtotalMoney - parseInt(this.freight) - parseInt(this.discountMoney)
          })
      }
    },
    // 
    generateOrder: function () {
      let arrys = [] // 
      this.productList.forEach((val, index) => {
        arrys.push({
          productId: val.productId,
          buyNum: val.buyNums,
          productName: val.productName,
          productPrice: val.productPrice,
          productPic: val.productInfo
        })
      })
      // 
      let data = {
        custId: this.$store.getters.getOpenid, // id
        dindanMoney: this.totalMoney, // 
        freight: this.freight, // 
        cutPrice: this.discountMoney, // 
        leaveMessage: this.lessMessage, // 
        addressId: this.address.addressId, // id
        dinDanInfoArry: JSON.stringify(arrys), // 
        flag: '0', // 
        addressInfo: this.address.addressInfo, // 
        addressName: this.address.name, // 
        addressPhone: this.address.phoneNum, // 
        addressProvince: this.address.province, // 
        addressCity: this.address.city, // 
        addressDistrict: this.address.district // 
      }
      this.axios.post('water/dindan/insert2', data)
        .then(res => {
          localStorage.setItem('orderId', res.data)
          localStorage.removeItem('selectedAddressId')
          localStorage.removeItem('proId')
          localStorage.removeItem('orderType')
          // localStorage.removeItem('buyNums')
          return this.axios.post('water/shopCar/delete', {shopCarId: localStorage.getItem('shopcarIdStr')})
        })
        .then(res => {
          localStorage.removeItem('shopcarIdStr')
          return this.axios.post('water/vip/list', {custId: this.$store.getters.getOpenid})
        })
        .then(res => {
          if (parseInt(res.data[0].user[0].vip.money) >= parseInt(this.totalMoney)) {
            this.$MessageBox.confirm('?')
              .then(action => {
                let data = {
                  custId: this.$store.getters.getOpenid,
                  money: '-' + this.totalMoney
                }
                this.axios.post('water/vip/updateMoneyUser', data)
                  .then(() => {
                    this.$toast('')
                    return this.axios.post('water/dindan/autoUpdate', {dindanId: localStorage.getItem('orderId')})
                  })
                  .then(res => {
                    localStorage.removeItem('selectedAddressId')
                    this.$router.push({name: 'CompleteOrder'})
                  })
                  .catch(() => {
                    this.$toast('')
                    localStorage.removeItem('orderId')
                    this.$router.push({name: 'CompleteOrder'})
                  })
              })
              .catch(() => {
                this.$router.push({name: 'OrderList', params: {orderType: '0'}})
              })
          } else {
            this.$MessageBox.alert('').then(action => {
              localStorage.removeItem('orderId')
              this.$router.push({name: 'OrderList', params: {orderType: '0'}})
            })
          }
        })
    }
  }
}
</script>

<style scoped>
p{
  text-align: left;
}
.container {
  background: #F8F8F8;
  padding-bottom: 6rem;
  overflow: auto;
}
.address-container {
  height: 9rem;
  width: 100%;
  padding-top: 40px;
  background: #FFFFFF;
  margin-bottom: 0.8rem;
}
/*  */
.address-icon {
  height: 100%;
  width: 15%;
  float: left;
}
.address-icon img {
  height: 1.8rem;
  margin: 0 auto;
  margin-top: 2rem;
}
/*  */
.address-info {
  height: 100%;
  width: 75%;
  float: left;
  padding: 0.7rem;
  font-size: 14px;
}
.address-info-name {
  float: left;
}
.address-info-phone {
  float: right;
}
.detail-adress {
  margin-top: 1.4rem;
  text-align: left;
}
/*  */
.none-address {
  line-height: 6.5rem;
  left: 13%;
  background: #FFFFFF;
  width: 75%;
  position: absolute;
  font-size: 18px;
  color: #7C7C6C;
}
/*  */
.address-selected {
  height: 100%;
  width: 10%;
  float: left;
}
.address-selected img {
  height: 1.3rem;
  margin: 0 auto;
  margin-top: 2.5rem;
}
.goods-container {
  width: 100%;
  background: #FFFFFF;
  padding-bottom: 0.6rem;
}
.company-name {
  text-align: left;
  font-size: 14px;
  line-height: 3rem;
  padding-left: 1rem;
}
.flag {
  background: red;
  color: #FFFFFF;
  padding: 2px;
  border-radius: 4px;
  font-size: 14px;
}
/*  */
.goods {
  height: 5.5rem;
  background: #f9f9f9;
  margin-bottom: 0.5rem;
}
/*  */
.goods-img {
  width: 30%;
  float: left;
  margin-top: 0.5rem;
  text-align: left;
  padding-left: 1rem;
}
.goods-img img {
  height: 4.5rem;
}
/*  */
.goods-info {
  width: 70%;
  height: 100%;
  padding-top: 0.5rem;
  float: left;
  position: relative;
}
/*  */
.goods-name {
  font-size: 14px;
  text-align: left;
}
/*  */
.goods-pice-nums {
  position: absolute;
  bottom: 0.5rem;
  line-height: 1rem;
  width: 100%;
  font-size: 14px;
}
.pice {
  color: red;
}
.nums {
  float: right;
  margin-right: 1rem;
}
/*  */
.freight-container {
  padding-left: 1rem;
  padding-right: 1rem;
  line-height: 2.5rem;
  margin-top: 0.5rem;
}
.freight-container p {
  border-bottom: 1px solid #ECECEC;
  font-size: 14px;
}
.freight {
  float: right;
}
/*  */
.message-container {
  font-size: 14px;
  padding-left: 1rem;
  padding-right: 1rem;
  line-height: 2.5rem;
  margin-top: 0.5rem;
}
.message-container p {
  border-bottom: 1px solid #ECECEC;
  font-size: 14px;
}
.message-container input {
  border: 0;
  height: 2rem;
  width: 74%;
  padding: 0.3rem;
  outline: none;
}
/*  */
.subtotal-container {
  height: 2.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  width: 100%;
  margin-top: 0.5rem;
}
.subtotal-container p {
  line-height: 2.5rem;
  font-size: 14px;
  float: right;
}
/*  */
.subtotal-money {
  color: red;
  font-size: 17px;
}
/*  */
.footer-container {
  position: fixed;
  bottom: 0;
  height: 4.5rem;
  width: 100%;
  background: #FFFFFF;
}
/*  */
.money-container {
  width:60%;
  line-height: 4.5rem;
  float: left;
  text-align: right;
  color: red;
  font-size: 14px;
  padding-right: 2rem;
}
.money {
  font-size: 17px;
}
/*  */
.genert-order {
  width:40%;
  line-height: 4.5rem;
  background-color: #00BFFF;
  color: #FFFFFF;
  float: left;
}
</style>



