<template>
<div class="container">
  <!--  -->
  <mt-header title="" fixed>
    <mt-button icon="back" slot="left" @click="back"></mt-button>
    <mt-button class="shopcar-but" slot="right" @click="showDelete">{{updateBtn}}</mt-button>
  </mt-header>
  <!--  -->
  <!--  -->
  <div class="mid-container">
    <transition-group name="slide-fade">
      <li class="shopcar-product" v-for="(val, index) in productList" :key="val.shopCarId" @click="toPage(val.product.productId)">
        <!--  -->
        <div class="check-box" @click.stop.prevent>
          <checkBox :index="index"  ></checkBox>
        </div>
        <!--  -->
        <!--  -->
        <div class="pic-container">
          <img :src="val.product.productInfo" alt="">
        </div>
        <!--  -->
        <!--  -->
        <div class="product-info">
          <p>{{val.product.productName}}</p>
          <span class="product-pice">{{'' + val.product.productPrice}}</span>
          <!--  -->
          <div class="product-nums">
            <span class="num-set" @click.stop.prevent="subtractProductNums(index)">-</span>
            <span class="num">{{val.buyNum}}</span>
            <span class="num-set num-set-add" @click.stop.prevent="addProductNums(index)">+</span>
          </div>
          <!--  -->
        </div>
        <!--  -->
      </li>
    </transition-group>
  </div>
  <!--  -->
  <!--  -->
  <div class="footer-container">
    <div class="generate-but"  @click="generateOrder()"></div>
    <transition name="fade">
      <div class="generate-but delete" v-show="!deleteShow" @click="deleteSelectedPro"></div>
    </transition>
    <div class="total-money">
      <span></span>
      <span class="money">{{'' + this.$store.getters.getTotalMoney }}</span>
    </div>
    <div class="all-check">
      <checkBox class="all-check-box"  :index="-1"></checkBox>
      <span></span>
    </div>
  </div>
  <!--  -->
</div>
</template>

<script>
import checkBox from '../common/CheckBox'
export default {
  components: {checkBox},
  data () {
    return {
      productList: [], // 
      deleteShow: true, // 
      updateBtn: ''
    }
  },
  created: function () {
    this.getShopcar()
  },
  // 
  beforeRouteLeave (to, from, next) {
    if (to.name === 'GenerateOrder') {
      localStorage.setItem('fromPath', from.path)
    }
    next()
    this.$store.commit('changeASelected')
  },
  methods: {
    // 
    back: function () {
      if (this.$route.params.type === 'shopCarTab') {
        this.$router.push({name: 'Home', params: {type: 'home'}})
      } else {
        // this.$router.back()
        this.$router.push({name: 'ProductDeatils', params: {productId: localStorage.getItem('productId')}})
      }
    },
    judgeUrl: function () {
      if (document.URL.split('dist')[1].length <= 1) {
        this.$router.back()
      }
    },
    // 
    getShopcar: function () {
      let data = {
        custId: this.$store.getters.getOpenid
      }
      this.axios.post('water/shopCar/list', data)
        .then(res => {
          res.data.forEach((val, index) => {
            // res.data[index].product.productInfo = require('../../assets' + val.product.productInfo)
            res.data[index].product.productInfo = '/static' + val.product.productInfo
            res.data[index].selected = false
          })
          this.productList = res.data
          this.$store.commit('initializeShopCarProduct', res.data)
        })
        .catch(() => {
          this.$toast('')
        })
    },
    // 
    addProductNums: function (index) {
      if (parseInt(this.productList[index].buyNum) === 3) {
        this.$toast('3')
        return
      }
      this.updateData(index, 'add')
    },
    // 
    subtractProductNums: function (index) {
      if (parseInt(this.productList[index].buyNum) === 1) {
        this.$toast('1')
        return
      }
      this.updateData(index, 'sub')
    },
    // (indexways)
    updateData: function (index, ways) {
      let data = {}
      if (ways === 'add') {
        data = {
          shopCarId: this.productList[index].shopCarId,
          num: parseInt(this.productList[index].buyNum) + 1
        }
      } else if (ways === 'sub') {
        data = {
          shopCarId: this.productList[index].shopCarId,
          num: parseInt(this.productList[index].buyNum) - 1
        }
      }
      this.axios.post('water/shopCar/num', data)
        .then(() => {
          if (ways === 'add') {
            this.productList[index].buyNum = parseInt(this.productList[index].buyNum) + 1
          } else if (ways === 'sub') {
            this.productList[index].buyNum = parseInt(this.productList[index].buyNum) - 1
          }
          this.$toast('')
        })
        .catch(() => {
          this.$toast('')
        })
    },
    // 
    isSelected: function (index) {
      this.productList[parseInt(index)].selected = !this.productList[parseInt(index)].selected
    },
    // 
    showDelete: function () {
      this.deleteShow = !this.deleteShow
      if (this.deleteShow) {
        this.updateBtn = ''
      } else {
        this.updateBtn = ''
      }
    },
    // 
    deleteSelectedPro: function () {
      this.$MessageBox.confirm('?')
        .then(action => {
          let data = {
            shopCarId: this.$store.getters.getShopcarIdStr
          }
          this.axios.post('water/shopCar/delete', data) // 
            .then(() => {
              let deleteArry = this.$store.getters.getShopcarIdStr.split(',')
              deleteArry.forEach((val, index) => {
                this.productList.forEach((res, index) => {
                  if (res.shopCarId === val) {
                    this.productList.splice(index, 1)
                  }
                })
              })
              this.$toast('')
            })
            .catch(() => {
              this.$toast('')
            })
        })
        .catch(() => {
        })
    },
    // 
    generateOrder: function () {
      let getShopcarIdStr = this.$store.getters.getShopcarIdStr
      if (getShopcarIdStr === null || getShopcarIdStr === '' || getShopcarIdStr === undefined) {
        return this.$toast('')
      }
      localStorage.setItem('shopcarIdStr', getShopcarIdStr)
      localStorage.setItem('orderType', 'shopCar')
      this.$router.push({name: 'GenerateOrder', params: {orderType: 'shopCar'}})
    },
    // 
    toPage: function (productId) {
      this.$router.push({name: 'ProductDeatils', params: {productId: productId}})
    }
  }
}
</script>

<style scoped>
.container {
  background: #FFFFFF;
}
.mid-container {
  padding-top: 40px;
  padding-bottom: 4rem;
}
/*  */
.shopcar-product {
  height: 6.5rem;
  position: relative;
  border-bottom: 1px solid #FCF8E3;
  background: #FFFFFF;
 }
 /*  */
 .check-box {
  display: inline-block;
  height:100% ;
  width: 10%;
  float: left;
  padding-top: 2rem;
  padding-bottom: 2rem;
 }
 /*  */
.pic-container {
  height: 100%;
  width: 25%;
  float: left;
}
.pic-container img {
  width: 98%;
}
/*  */
.product-info {
  height: 100%;
  width: 65%;
  float: left;
  padding: 0.6rem;
  font-size: 14px;
  text-align: left;
  color: #7C7C7C;
}
/*  */
.product-pice {
  position: absolute;
  bottom: 1.5rem;
  color: red;
  font-size: 16px;
}
/*  */
.product-nums {
  position: absolute;
  bottom: 1.5rem;
  right: 1rem;
}
.product-nums span {
  display: block;
  float: left;
  line-height: 1.2rem;
  text-align: center;
  padding-bottom: 0.2rem;
  border-radius: 3px;
}
.num-set {
  width: 1.2rem;
  font-size: 25px;
  background-color: #f3f3f3;
}
.num {
  font-size: 15px;
  padding: 0 0.5rem;
}
.product-nums .num-set-add {
  font-size: 20px;
  padding: 0.2rem 0 0rem;
}
/*  */
.footer-container {
  background-color: #FFFFFF;
  position: fixed;
  height: 4rem;
  width: 100%;
  bottom: 0;
}
.footer-container div {
  width: 35%;
  float: right;
  line-height: 4rem;
}
/*  */
.generate-but {
  background-color: #00BFFF;
  color: #FFFFFF;
  text-align: center;
}
.delete {
  background-color: red;
  position: absolute;
  right: 0;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
.deleteShow {
  display: none;
}
.money {
  color: red;
}
.footer-container .all-check {
  line-height: 4rem;
  height: 4rem;
  width: 30%;
  position: relative;
}
.all-check-box {
  position: absolute;
  left: 0rem;
}
/*  */
/*  */
.slide-fade-enter-active {
  transition: all .5s ease;
}
.slide-fade-leave-active {
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to {
  transform: translateY(700px);
  opacity: 0;
}
</style>



