import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/nav/Home'
import ProductDeatils from '@/components/product/ProductDetails' // 
import ShopCar from '@/components/shopcar/ShopCar' // 
import UpdateUserInfo from '@/components/myinfo/UpdateUserInfo' // 
import AddressManager from '@/components/address/AddressManager' // 
import AddAddress from '@/components/address/AddAddress' // 
import UpdateAddress from '@/components/address/UpdateAddress' // 
import GenerateOrder from '@/components/order/GenerateOrder' // 
import CompleteOrder from '@/components/order/CompleteOrder' // 
import OrderList from '@/components/order/OrderList' // 
import OrderDetails from '@/components/order/OrderDetails' // 
import QueryLogistics from '@/components/order/QueryLogistics' // 
import IntergralProList from '@/components/integralmall/IntegralProList' // 
import IntegralProDetails from '@/components/integralmall/IntegralProDetails' // 
import IntegralOrder from '@/components/integralmall/IntegralOrder' // 
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { // 
      path: '/app/:type',
      name: 'Home',
      component: Home,
      meta: {
        keepAlive: true // 
      }
    },
    { // 
      path: '/ShopCar/:type',
      name: 'ShopCar',
      component: ShopCar,
      meta: {
        keepAlive: false // 
      }
    },
    { // 
      path: '/ProductDeatils/:productId',
      name: 'ProductDeatils',
      component: ProductDeatils,
      meta: {
        keepAlive: true // 
      }
    },
    { // 
      path: '/UpdateUserInfo',
      name: 'UpdateUserInfo',
      component: UpdateUserInfo,
      meta: {
        keepAlive: false // 
      }
    },
    { // 
      path: '/Address/:typed',
      name: 'AddressManager',
      component: AddressManager,
      meta: {
        keepAlive: false // 
      }
    },
    { // 
      path: '/AddAddress',
      name: 'AddAddress',
      component: AddAddress,
      meta: {
        keepAlive: false // 
      }
    },
    { // 
      path: '/UpdateAddress/:addressId',
      name: 'UpdateAddress',
      component: UpdateAddress,
      meta: {
        keepAlive: false // 
      }
    },
    {// 
      path: '/GenerateOrder/:orderType',
      name: 'GenerateOrder',
      component: GenerateOrder,
      meta: {
        keepAlive: false // 
      }
    },
    {// 
      path: '/CompleteOrder',
      name: 'CompleteOrder',
      component: CompleteOrder,
      meta: {
        keepAlive: false // 
      }
    },
    {// 
      path: '/OrderList/:orderType',
      name: 'OrderList',
      component: OrderList,
      meta: {
        keepAlive: false // 
      }
    },
    {// 
      path: '/OrderDetails/:orderId',
      name: 'OrderDetails',
      component: OrderDetails,
      meta: {
        keepAlive: false // 
      }
    },
    {// 
      path: '/QueryLogistics/:orderId',
      name: 'QueryLogistics',
      component: QueryLogistics,
      meta: {
        keepAlive: false // 
      }
    },
    {// 
      path: '/IntergralProList',
      name: 'IntergralProList',
      component: IntergralProList,
      meta: {
        keepAlive: false // 
      }
    },
    {// 
      path: '/IntegralProDetails/:productId',
      name: 'IntegralProDetails',
      component: IntegralProDetails,
      meta: {
        keepAlive: false // 
      }
    },
    {// 
      path: '/IntegralOrder/:productId',
      name: 'IntegralOrder',
      component: IntegralOrder,
      meta: {
        keepAlive: false // 
      }
    }
  ]
})



