<template>
  <div class="container">
    <!--  -->
    <transition name="slide-fade">
      <loadingPage v-if="loaderShow"></loadingPage>
    </transition>
    <!--  -->
    <!--  -->
    <transition name="gradual">
      <maskLayer  v-if="setNumShow"></maskLayer>
    </transition>
    <transition name="slide-fade">
      <seclectProductNum v-on:listenToChildEvent="showFlag" v-if="setNumShow" :setNumShow="setNumShow" :productDetail="productDetail" :buyType="buyType" @toClose="closeSetNumWindow"></seclectProductNum>
    </transition>
    <!--  -->
    <!--  -->
    <mt-header title="" fixed>
      <mt-button icon="back" slot="left" @click="back"></mt-button>
      <mt-button class="shopcar-but" slot="right" @click="goShopCar"></mt-button>
    </mt-header>
    <!--  -->
    <!--  -->
    <div class="container-mid">
      <!--  -->
      <div class="swipe-container">
        <mt-swipe :auto="3000">
          <mt-swipe-item v-for="(val) in productDetail.proPic" :key="val.picId">
            <img :src="val.pic" alt="">
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <!--  -->
      <!--  -->
      <div class="product-title">
        <p class="product-name-container">
          <span class="product-ziyinflag"></span>
          <span class="product-name" v-cloak>{{productDetail.productName}}</span>
        </p>
        <p class="product-price" v-cloak>{{'' + productDetail.productPrice}}</p>
        <p class="product-info">
          <span>0</span>
          <span v-cloak>{{'' + productDetail.productNum}}</span>
          <span></span>
        </p>
      </div>
      <!--  -->
      <!--  -->
      <div class="product-introduction-one">
        <li>
          <img src="../../assets/icon/productDetail/yanfa.svg" alt="">
          <p></p>
        </li>
        <li>
          <img src="../../assets/icon/productDetail/jianyan.svg" alt="">
          <p></p>
        </li>
        <li>
          <img src="../../assets/icon/productDetail/qvdao.svg" alt="">
          <p></p>
        </li>
        <li>
          <img src="../../assets/icon/productDetail/pinzhi.svg" alt="">
          <p></p>
        </li>
      </div>
      <!--  -->
      <!--  -->
      <div class="product-introduction-two">
        <img src="/static/img/productIntroduction/ddw_jiesao.jpg" alt="">
      </div>
      <!--  -->
    </div>
    <!--  -->
    <!--  -->
    <div class="footer-container">
      <li class="footer-kefu" @click="customService()">
        <img src="../../assets/icon/productDetail/kefu.svg" alt="">
        <p></p>
      </li>
      <li  class="footer-addshopcar" @click="addShopcar"></li>
      <li class="footer-buy"  @click="buyNow"></li>
    </div>
    <!--  -->
  </div>
</template>

<script>
import loadingPage from '../common/LoadingPage' // 
import seclectProductNum from './SeclectProductNum' // 
import maskLayer from './MaskLayer' // 
export default {
  components: {loadingPage, seclectProductNum, maskLayer},
  data () {
    return {
      productDetail: {}, // 
      loaderShow: true, // 
      setNumShow: false, // 
      buyType: '' // 01
    }
  },
  created: function () {
    this.getProductDetail()
  },
  beforeRouteEnter: function (to, from, next) {
    if (from.name === 'ShopCar') {
      localStorage.setItem('fromName', from.name)
    } else if (from.name === 'Home' && from.params.type === 'home') {
      localStorage.setItem('fromName', 'Home-home')
    }
    next((vm) => {
      vm.loaderShow = true
      vm.getProductDetail()
    })
  },
  beforeRouteLeave: function (to, from, next) {
    this.setNumShow = false
    if (to.name === 'GenerateOrder') {
      localStorage.setItem('fromPath', from.path)
    }
    next(vm => {
    })
  },
  methods: {
    // 
    back: function () {
      if (localStorage.getItem('fromName') === 'ShopCar') {
        this.$router.push({name: 'ShopCar', params: {type: 'shopCarTab'}})
      } else if (localStorage.getItem('fromName') === 'Home-home') {
        this.$router.push({name: 'Home', params: {type: 'home'}})
      } else {
        this.$router.push({name: 'Home', params: {type: 'product'}})
      }
    },
    // 
    goShopCar: function () {
      localStorage.setItem('productId', this.$route.params.productId)
      this.$router.push({name: 'ShopCar', params: {type: 'shopCar'}})
    },
    // 
    getProductDetail: function () {
      let data = {
        productId: this.$route.params.productId
      }
      this.axios.post('water/product/info', data)
        .then(res => {
          res.data[0].proPic.forEach((val, index) => {
            // var context = require.context('../../assets', true, /\.jpg$/) console.log(context.keys())
            // res.data[0].proPic[index].pic = require('../../assets' + val.pic) // context(val.pic)
            res.data[0].proPic[index].pic = '/static' + val.pic // context(val.pic)
          })
          this.productDetail = res.data[0]
          setTimeout(() => {
            this.loaderShow = false
          }, 500)
        })
        .catch(() => {
          this.$toast('')
          this.loaderShow = false
        })
    },
    // 
    addShopcar: function () {
      this.openSetNumWindow()
      this.buyType = '0' // 0
    },
    // 
    buyNow: function () {
      this.openSetNumWindow()
      this.buyType = '1' // 1
    },
    // 
    closeSetNumWindow: function () {
      this.setNumShow = false
    },
    // 
    openSetNumWindow: function () {
      this.setNumShow = true
    },
    // 
    showFlag: function (data) {
      if (data === 'close') {
        this.setNumShow = false
      }
    },
    // 
    customService: function () {
      this.$toast('0571-83819690')
    }
  }
}
</script>

<style scoped>
  /*  */
  /*  */
  .slide-fade-enter-active {
    transition: all .5s ease;
  }
  .slide-fade-leave-active {
    transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-leave-to {
    transform: translateY(700px);
    opacity: 0;
  }
  /*  */
  .gradual-enter-active, .gradual-leave-active {
    transition: opacity .8s;
  }
  .gradual-enter, .gradual-leave-to {
    opacity: 0;
  }
  /*  */
  .container {
    height: 100%;
  }
  /*  */
  .shopcar-but {
    background: url(../../assets/icon/productDetail/shopcar.svg) no-repeat center center;
    width: 1.5rem;
    background-size: contain;
  }
  /*  */
  .container-mid {
    height: 100%;
    padding-top: 40px;
    background-color: #F3F3F3;
    -webkit-overflow-scrolling: touch;
    overflow: auto;
  }
  /*  */
  .swipe-container {
    height: 330px;
    background: #fff;
  }
  .swipe-container img {
    width: 90%;
  }
  /*  */
  .product-title {
    width: 100%;
    height: 9rem;
    background-color: #FFFFFF;
    margin-top: 0.8rem;
    padding: 1.6rem 1rem;
  }
  .product-name-container {
    text-align: left
  }
  /*  */
  .product-ziyinflag {
    width: 2.3rem;
    height: 1.3rem;
    line-height: 1.4rem;
    display: inline-block;
    text-align: center;
    background-color: red;
    color: #FFFFFF;
    border-radius: 5px;
    font-size: 13px;
  }
  .product-name {
    font-size: 18px;
    color: #7c7c6c;
    margin-left: 0.5rem;
  }
  .product-price {
    margin-top: 1rem;
    text-align: left;
    color: red;
  }
  .product-info {
    text-align: left;
    color: #7c7c7c;
    font-size: 13px;
    margin-top: 1rem;
  }
  .product-info span {
    margin-right: 1.8rem;
  }
  /*  */
  .product-introduction-one {
    height: 5rem;
    width: 100%;
    background-color: #FFFFFF;
    margin-top: 0.8rem;
    padding: 0.8rem 0 0.5rem;
  }
  .product-introduction-one li {
    width: 25%;
    height: 100%;
    float: left;
  }
  .product-introduction-one li img {
    height: 50%;
  }
  .product-introduction-one li p {
    color: #7C7C6C;
    font-size: 13px;
    margin-top: 0.4rem;
  }
  /*  */
  .product-introduction-two {
    margin: 0.8rem 0 3.5rem;
  }
  .product-introduction-two img {
    width: 100%;
  }
  /*  */
  .footer-container {
    height: 3.4rem;
    width: 100%;
    background-color: #FFFFFF;
    position: fixed;
    bottom: 0;
  }
  .footer-container li {
    float: left;
    text-align: center;
    line-height: 3.4rem;
  }
  .footer-container .footer-kefu {
    width: 30%;
    line-height: 1.2rem;
  }
  .footer-container .footer-kefu img {
    width: 1.3rem;
    margin-top: 0.3rem;
  }
  .footer-container .footer-kefu p {
    font-size: 14px;
    color: #7c7c7c;
  }
  .footer-container .footer-addshopcar {
    width: 35%;
    background-color: #00BFFF;
    opacity: 0.8;
    color: #FFFFFF;
  }
  .footer-container .footer-buy {
    width: 35%;
    background-color: #00BFFF;
    color: #FFFFFF;
  }
</style>



